<template>
  <div>
    <van-nav-bar title="题库选择" left-arrow @click-left="onClickLeft" fixed />
    <div class="Zpy_head">
      <van-tabs v-model="active">
        <van-tab
          :title="item.name"
          v-for="(item, index) in Zpy_titleList"
          :key="index"
          :name="item.id"
        ></van-tab>
      </van-tabs>
    </div>
    <p class="Zpy_title">
      <span>按时间</span>
      <span @click="Zpy_hot">按热度</span>
      <span>
        按价格
        <van-icon name="arrow-up" @click="Zpy_price2" />
        <van-icon name="arrow-down" @click="Zpy_price" />
      </span>
    </p>
    <div class="Zpy_contentList">
      <ul>
        <li
          v-for="(item, index) in Zpy_List"
          :key="index"
          @click="Zpy_goDeatil(item.id)"
        >
          <img :src="item.cover_img" alt="" />
          <div>
            <p class="Zpy_name">{{ item.name }}</p>
            <p class="Zpy_author">作者：{{ item.author }}</p>
            <p class="Zpy_free">{{ item.price / 100 }}</p>
          </div>
        </li>
      </ul>
    </div>
  </div>
</template>
<script>
import { getClassify, getList } from "@/utils/api";
export default {
  data() {
    return {
      value: "",
      activeKey: "",
      Zpy_titleList: [],
      active: 0,
      Zpy_List: [],
    };
  },
  methods: {
    onClickLeft() {
      this.$router.go(-1);
    },
    Zpy_Bookdetail(id) {
      this.$router.push({ path: "/book", query: { id } });
    },
  },
  mounted() {
    // 专练分类
    getClassify().then((res) => {
      // console.log(res);
      this.Zpy_titleList = res;
    });
    //专练列表内容
    getList().then((res) => {
      console.log(res);
      this.Zpy_List = res.list;
    });
  },
  methods: {
    onClickLeft() {
      this.$router.back();
    },
    Zpy_price() {
      this.Zpy_List.sort((a, b) => {
        return b.price - a.price;
      });
    },
    Zpy_price2() {
      this.Zpy_List.sort((a, b) => {
        return a.price - b.price;
      });
    },
    Zpy_hot() {
      this.Zpy_List.sort((a, b) => {
        return b.sales_base - a.sales_base;
      });
    },
    Zpy_time() {},
    Zpy_goDeatil(id) {
      this.$router.push({ path: "/book", query: { id } });
    },
  },
};
</script>
<style lang="scss" scoped>
.van-search__content {
  margin-top: 1rem;
}
.van-tabs {
  margin-top: 1rem;
}
.Zpy_head{
  height: 8vh;
  width: 100%;
}
.Zpy_title {
  height: 1rem;
  line-height: 1rem;
  font-size: 0.3rem;
  span {
    margin-left: 0.4rem;
  }
}
.Zpy_contentList {
  width: 100%;
  height: auto;
  padding: 0.2rem 0.3rem;
  ul {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    li {
      width: 100%;
      height: 2rem;
      display: inline-flex;
      border-radius: 0.2rem;
      border: 1px solid #dddddd;
      margin-top: 0.2rem;
      img {
        width: 30%;
      }
      div {
        width: 70%;
        padding: 0.2rem 0.2rem;
        .Zpy_name {
          font-size: 0.3rem;
        }
        .Zpy_author {
          color: grey;
          margin-top: 0.2rem;
          margin-bottom: 0.5rem;
        }
        .Zpy_free {
          color: red;
          font-size: 0.3rem;
        }
      }
    }
  }
}
</style>